@use '../utils/' as u;

.footer {
    background-color: hsl(var(--clr-primary-dark));
    // background: linear-gradient(90deg, hsl(var(--clr-primary-dark)) 50%, hsl(var(--clr-accent)) 85%);
    clip-path: polygon(0 10%, 41% 0, 100% 26%, 100% 100%, 0 100%);
    
    .container {
        @include u.container;
        text-align: center;
        padding: 4em 0 3em;
    }

    &__copyright {
        font-size: 1.125em;
        color: hsla(var(--clr-white), .95);
        font-weight: var(--fw-regular);
        margin-bottom: 0;
    }
}